import { MAPBOX_ACCESS_TOKEN } from './mapConfig';

export const lngLat = position => {
    const [lng = 0, lat = 0] = position;
    return [+lat, +lng];
};

export const getRandomNum = num => `${(Math.random() * 10 ** num).toFixed(0)}`;

export const addSuperMap = leafletMap => {
    const url = `http://support.supermap.com.cn:8090/iserver/services/map-china400/rest/maps/China`;
    return L.supermap.tiledMapLayer(url).addTo(leafletMap);
};

export const addBing = leafletMap =>
    L.tileLayer
        .bing('AuhiCJHlGzhg93IqUH_oCpl_-ZUrIE6SPftlyGYUvr9Amx5nzA-WqGcPquyFZl4L')
        .addTo(leafletMap);

export const addOSM = leafletMap =>
    L.tileLayer('http://{s}.tile.osm.org/{z}/{x}/{y}.png', {
        maxZoom: 18,
        attribution:
            'Map data &copy; <a href="http://openstreetmap.org">OpenStreetMap</a> contributors, <a  href="http://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>',
    }).addTo(leafletMap);

export const addGIBS = leafletMap =>
    new L.GIBSLayer('MODIS_Aqua_SurfaceReflectance_Bands721', {
        date: new Date('2020/04/01'),
        transparent: true,
    }).addTo(leafletMap);

export const addMapbox = leafletMap => {
    const url = `https://api.mapbox.com/styles/v1/hansjjjjjj/cl3wnoefq000d14nxd1i2tiwm/tiles/{z}/{x}/{y}?access_token=${MAPBOX_ACCESS_TOKEN}`;
    return L.tileLayer(url, {
        attribution:
            'Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, Imagery © <a href="https://www.mapbox.com/">Mapbox</a>',
        tileSize: 512,
        maxZoom: 18,
        zoomOffset: -1,
        id: 'mapbox/streets-v11',
        accessToken: MAPBOX_ACCESS_TOKEN,
    }).addTo(leafletMap);
};
