/* eslint-disable class-methods-use-this */
class ModeBase {
    constructor(leafletMap) {
        this.leafletMap = leafletMap;
        this.mode = '';
        this.currentIndex = 0;
        this.coordinates = [];
    }

    getLngLat(e) {
        return [e.latlng.lat, e.latlng.lng];
    }

    init(type) {
        this.mode = type;
    }

    onClick(e) {}

    onMouseMove(e) {}

    onDoubleClick(e) {}
}
export default ModeBase;
