export default class LayerController {
    constructor(leafletMap) {
        this.leafletMap = leafletMap;
        window.layerController = this;
        this._layerList = new Map([]);
    }

    addLayerGroup(layerName, markerList) {
        let layerGroup = this._layerList.get(layerName);
        if (layerGroup) {
            markerList.forEach(item => {
                layerGroup.addLayer(item).addTo(this.leafletMap);
            });
        } else {
            layerGroup = L.layerGroup(markerList).addTo(this.leafletMap);
            this._layerList.set(layerName, layerGroup);
        }
    }

    getLayerData(layerName) {
        const layer = this._layerList.get(layerName);
        if (layer) return layer.getLayers();
        return [];
    }

    removeLayerData(layerName) {
        const layer = this._layerList.get(layerName);
        if (layer) {
            layer.remove();
            this._layerList.delete(layerName);
            return true;
        }
        return false;
    }

    addGeoJson(
        type,
        json,
        style = {
            weight: 2, // 外边框宽度
            opacity: 1, // 外边框透明度
            color: '#2f8ffc', // 外边框颜色
            fillOpacity: 0.2, // 填充透明度
            fillColor: '#6ba7de', // 填充的颜色
        }
    ) {
        const layer = L.geoJSON(json, {
            style,
            onEachFeature: (f, l) => {
                const popup = L.popup().setContent(
                    `<p>${f.properties.name}（${f.properties.QCODE}）</p>`
                );
                l.on({
                    mouseover: e => {
                        // 改变样式
                        l.setStyle({
                            color: '#fed714', // 外边框颜色
                            fillOpacity: 0.2, // 填充透明度
                            fillColor: '#007acc', // 填充的颜色
                        });
                        l.bringToFront(); // 置顶被选中的流程
                        // 添加弹窗
                    },
                    mouseout: e => {
                        l.setStyle({
                            color: '#2f8ffc', // 外边框颜色
                            fillOpacity: 0.2, // 填充透明度
                            fillColor: '#6ba7de', // 填充的颜色
                        });
                        l.bringToBack();
                    },
                    click: e => {
                        console.log(`rdapp -> LayerController -> e`, e);

                        popup.setLatLng([+e.latlng.lat, +e.latlng.lng]).openOn(this.leafletMap);
                    },
                });
            },
        }).addTo(this.leafletMap);
        this.addLayerGroup(type, [layer]);
    }
}
