import ModeBase from './base';

class Line extends ModeBase {
    constructor(leafletMap) {
        super(leafletMap);
        this.feature = L.polyline([], { color: 'red' }).addTo(this.leafletMap);
    }

    onClick(e) {
        this.coordinates.push(this.getLngLat(e));
        this.currentIndex += 1;
    }

    onMouseMove(e) {
        if (this.currentIndex !== 0)
            this.feature.setLatLngs([...this.coordinates, this.getLngLat(e)]);
    }

}

export default Line;
