import * as turf from '@turf/turf';
import { lngLat, addMapbox, addBing, addGIBS, addOSM, addSuperMap, getRandomNum } from './mapUtils';
import { mapCenter } from './mapConfig';
import LayerController from './layerController';
import MapDraw from './mapDraw/mapDraw';
import boundaryJson from '../assets/json/map_mini.json';

class MapSdk {
    constructor() {
        window.mapSdk = this;
        window.turf = turf;
        this.leafletMap = null; // 地图对象
        this.currentTileLayer = null; // 当前显示底图图层
        this.layerController = null; // 点位图层管理器
        this.mapDraw = null; // 绘制
    }

    addRadarImage() {
        const imgList = [
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_6_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_12_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_18_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_24_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_30_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_36_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_42_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_48_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_54_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_60_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_66_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_72_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_78_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_84_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_90_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_96_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_102_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_108_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_114_2500_Mercator.png',
            'https://aqs.121.com.cn/fwzt/TREC_GD_HD/20220708/Meta_TREC_GD_202207081054_120_2500_Mercator.png',
        ];
        const imageBounds = [
            [19.0419, 108.505],
            [26.0419, 117.505],
        ];
        let image = null;
        let index = 0;
        const timeInterval = setInterval(() => {
            const imageUrl = imgList[index];
            const newImage = L.imageOverlay(imageUrl, imageBounds, { opacity: 0.5 }).addTo(
                this.leafletMap
            );
            console.log(`rdapp -> MapSdk -> setInterval -> newImage`, newImage);
            newImage.once('load', () => {
                if (image) image.remove();
                image = newImage;
            });
            index += 1;
            if (index >= imgList.length) index = 0;
        }, 2000);
    }

    // 添加点位图层
    addLayer(layerName, list, options = {}) {
        const marketList = [];
        list.forEach(item => {
            const {
                id,
                lng,
                lat,
                iconUrl = 'http://172.17.136.54:30015/staticRecourse/mapRecourse/marker/point.png',
                iconSize = [22, 30],
            } = item;
            const iconAnchor = [iconSize[0] / 2, iconSize[1]];
            if (!lng || !lat) return;
            const icon = L.icon({
                iconUrl,
                iconSize,
                iconAnchor,
            });
            const marker = L.marker(lngLat([+lng, +lat]), { icon }).addTo(this.leafletMap);
            marker.id = id ?? getRandomNum(6);
            marketList.push(marker);
        });
        this.layerController.addLayerGroup(layerName, marketList);
    }

    // 获取点位图层数据
    getLayer(layerName) {
        return this.layerController.getLayerData(layerName);
    }

    // 移除图层
    removeLayer(layerName) {
        return this.layerController.removeLayerData(layerName);
    }

    getRandomPoint(
        num = 10,
        range = [113.78326671101367, 22.745758443155403, 114.38124145453443, 22.57083493001]
    ) {
        const points = turf.randomPoint(num, { bbox: range });
        this.addLayer(
            'test',
            points.features.map(i => {
                const [lng, lat] = i.geometry.coordinates;
                return {
                    lng,
                    lat,
                    iconUrl:
                        'http://172.17.136.54:30015/staticRecourse/mapRecourse/marker/重点防护目标/医院.png',
                };
            })
        );
    }

    // 初始化地图
    initMap() {
        this.leafletMap = L.map('map', {
            zoomControl: false,
            center: lngLat(mapCenter),
            zoom: 11,
        });
        this.switchMap('Mapbox');
        this.layerController = new LayerController(this.leafletMap);
        this.mapDraw = new MapDraw(this.leafletMap);
        this.getRandomPoint();
        this.layerController.addGeoJson('boundary', boundaryJson);
    }

    // 切换地图底图
    switchMap(layerName) {
        if (this.currentTileLayer) this.currentTileLayer.remove();
        const tileLayerList = new Map([
            ['Mapbox', addMapbox],
            ['GIBS', addGIBS],
            ['Bing', addBing],
            ['SuperMap', addSuperMap],
            ['OSM', addOSM],
        ]);
        this.currentTileLayer = tileLayerList.get(layerName)(this.leafletMap);
    }

    // 订阅地图事件
    subscribeMapEvent(fun, type = 'click', once = false) {
        this.leafletMap[once ? 'once' : 'on'](type, fun);
    }

    // 取消地图事件
    unsubscribeMapEvent(fun, type = 'click') {
        this.leafletMap.off(type, fun);
    }
}
const mapSdk = new MapSdk();
export default mapSdk;
