import Line from './mode/line';

export default class MapDraw {
    constructor(leafletMap) {
        this.leafletMap = leafletMap;
        window.mapDraw = this;
        this._layers = new Map([]);
    }

    changeMode(mode, options) {
        const modeList = new Map([['line', this.addLine()]]);
        modeList.get(mode);
    }

    addLine() {
        const obj = new Line(this.leafletMap);
        window.test = obj.onClick.bind(obj);
        this.leafletMap.on({
            click: obj.onClick.bind(obj),
            mousemove: obj.onMouseMove.bind(obj),
            dblclick: () => {
                console.log('HJJ dblclick', this.leafletMap);
                this.leafletMap.off({
                    click: obj.onClick.bind(obj),
                    mousemove: obj.onMouseMove.bind(obj),
                    dblclick: this,
                });
            },
        });
    }
}
